interface SectionHeadingProps {
  title: string
  subtitle?: string
  centered?: boolean
}

export function SectionHeading({ title, subtitle, centered = true }: SectionHeadingProps) {
  return (
    <div className={`space-y-3 ${centered ? "text-center" : ""}`}>
      <h2 className="text-3xl md:text-4xl lg:text-5xl font-bold text-[#2C4F5E]">{title}</h2>
      {subtitle && <p className="text-lg text-muted-foreground max-w-2xl mx-auto leading-relaxed">{subtitle}</p>}
    </div>
  )
}
